body {
  background-color: white;
  scroll-behavior: smooth;
}

.hero-main {
  height: 100vh;
  background-image: url(../../assets/images/coding_programming.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-position: top;
  position: relative;
}

.hero-wrapper {
  height: 100vh;
  width: 90% !important;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.header1 {
  color: white;
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

form {
  display: inline !important;
}

.search-hero {
  margin-right: 0px !important;
}

.p-affordable {
  font-size: 25px;
}

.autocomplete {
  width: 80%;
  background-color: white;
  border: none!important;
}

.auto-text {
  border: none!important;
}

.btn-search-hero {
  background: #12c3cb !important;
  color: white !important;
  background-image: linear-gradient(
    270deg,
    #ffd500,
    #fffb00,
    rgba(218, 218, 218, 0),
    rgba(175, 175, 175, 0)
  ) !important;
  border-radius: 30px !important;
  background-position: 1% 50% !important;
  background-size: 300% 300% !important;
  transition: all 0.5s ease-out !important;
}

.btn-search-hero:hover {
  border-color: transparent !important;
  color: #161616 !important;
  background-position: 99% 50% !important;
}

.btn-container-hero {
  display: flex;
  padding-left: 0px;
  justify-content: start !important;
}

.css-1d3z3hw-MuiOutlinedInput-notchedOutline{
  border: none!important;
}

.advert-main {
  height: 100%;
}

.advert-container {
  width: 100%!important;
}

.advert-wrapper {
  width: 90% !important;
  margin-left: auto;
  margin-right: auto;
  text-align: right !important;
}

.text-advert-container {
  text-align: start;
  margin-top: 100px !important;
}

.advert-video {
  width: 600px;
  margin-top: 100px;
  margin-bottom: 100px !important;
}

.how-itworks-main {
  height: 100%;
  background-color: #e9fafc;
  padding-bottom: 100px;
}

.howitworks-wrapper {
  text-align: center;
  width: 60%;
  margin-right: auto;
  margin-left: auto;
}

.howitworks-header {
  margin-top: 100px !important;
  color: #12c3cb;
  font-size: 60px !important;
}

.howitworks-img {
  width: 400px;
}

.howitworks-section-header {
  color: rgb(107, 107, 107);
}

.btn-getstarted {
  background-color: orange !important;
}

.benefits-main {
  height: 100%;
  padding-bottom: 100px;
}

.benefits-wrapper {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.whyus-header {
  margin-top: 100px;
  color: #12c3cb;
  font-size: 60px !important;
}

.whyus-img {
  width: 500px;
}

.startnow-main {
  height: 60vh;
  background-image: url(../../assets/images/banner.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  position: relative;
}

.startnow-wrapper {
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  color: rgb(0, 0, 0);
  text-align: center;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.student-container {
  background-color: white;
  height: 300px;
}

.tutor-container {
  background-color: #12c3cb;
  height: 300px;
}

li {
  list-style: none;
}

.signup-btn {
  background-color: orange !important;
  margin: 20px;
}

@media (max-width: 1325px) {
  .advert-video{
    width: 400px;
  }

  .howitworks-wrapper {
    width: 80%;
  }
}

@media (max-width: 1165px) {
  .btn-search-hero {
    background-size: 400% 400% !important;
    font-size: 15px!important;
  }
}

@media (max-width: 1024px) {
  .advert-video{
    width: 300px;
  }

  .video-header {
    font-size: 20px!important;
  }

  .video-text {
    font-size: 10px;
  }

  .whyus-img{
    width: 350px;
  }

  .startnow-wrapper {
    width: 90%;
  }
}

@media (max-width: 875px) {
  .howitworks-img{
    width: 300px;
  }
}


@media (max-width: 768px) {
  .autocomplete {
    width: 70%;
  }

  .advert-video{
    width: 500px;
    margin-bottom: 20px!important;
  }

  .header1{
    width: 100%;
  }

  .advert-wrapper{
    text-align: center!important;
  }

  .text-advert-container {
    text-align: center;
    margin-top: 0px !important;
  }

  .video-header {
    font-size: 30px!important;
  }

  .video-text {
    font-size: 18px;
  }

  .whyus-img {
    width: 500px;
    text-align: center;
  }

  .startnow-main {
    height: 90vh;
  }

  .student-container {
    height: 220px;
  }
  
  .tutor-container {
    height: 220px;
  }
}

@media (max-width: 565px) {

  .advert-video{
    width: 90%!important;
  }

  .whyus-img {
    width: 80%;
  }
}

@media (max-width: 491px) {

  .student-container {
    height: 250px;
  }
  
  .tutor-container {
    height: 250px;
  }
}


