.profile-main {
  padding-top: 150px;
  padding-bottom: 150px;
  height: 100%;
}

.profile-wrapper {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  box-shadow: 0 5px 15px rgba(179, 179, 179, 0.5);
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.4);
  z-index: 3;
}

@supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
  .profile-wrapper {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(5px);
  }
}

.login-p-error {
  width: 70%;
  color: red;
}

.verified-img {
  margin-top: 10px;
  width: 80px !important;
}

.tutorProfile-main {
  height: 100%;
  padding-top: 200px;
  padding-bottom: 200px;
  background-image: url("../../assets/images/profilebg.png");
  /* background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 30%, rgba(18,195,203,1) 30%); */
}

.tutorProfile-header {
  text-align: center;
  margin-top: 50px;
  margin-bottom: 80px;
}

.profile-img-tutor-result {
  border-radius: 10px;
  width: 300px;
  margin: 30px !important;
}

.tutorProfile-lowercontainer {
  width: 60% !important;
  margin-left: auto;
  margin-right: auto;
  text-align: start;
}

.small-box {
  background-color: rgb(194, 241, 243);
  margin-left: 20px;
  border-radius: 20px;
}

.contact-profile {
  margin-left: 10px;
}

.contact-profile-box {
  background-color: rgb(194, 241, 243);
  border-radius: 20px;
  padding-left: 20px;
}

.contact-header {
  padding-top: 10px !important;
  font-size: 25px !important;
  color: rgb(9, 109, 114);
}

.contact-span {
  font-size: 20px !important;
  color: black;
}

.rate-box {
  margin-left: 10px;
  margin-bottom: 50px;
  background-color: rgb(194, 241, 243);
  border-radius: 20px;
}

.rate-header {
  color: rgb(78, 78, 78);
  padding: 20px;
  font-size: 20px !important;
}

.rate-icon {
  color: rgb(5, 106, 131);
  margin-top: 10px;
  margin-left: 20px;
}

.box-icon {
  color: rgb(5, 106, 131);
  margin-top: 10px;
}

.p-rate {
  padding-left: 20px;
  font-size: 30px;
}

.profile-box-header-result {
  color: rgb(78, 78, 78);
  font-size: 25px !important;
}

.p-box {
  font-size: 20px !important;
}

.about-me {
  margin-top: 50px !important;
  margin-bottom: 50px !important;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.p-about {
  font-size: 20px !important;
}

@media (max-width: 1370px) {
  .profile-wrapper {
    width: 90% !important;
  }
}

@media (max-width: 1170px) {
  .profile-img-tutor-result {
    width: 200px;
  }

  .profile-box-header-result{
    font-size: 20px!important;
  }

  .about-me {
    width: 80%!important;
  }
}

@media (max-width: 910px) {
  .profile-box-header-result{
    font-size: 15px!important;
  }

  .p-about{
    font-size: 10px;
  }
}

@media (max-width: 768px) {
  .contact-profile-box {
    margin-right: auto;
    margin-left: auto;
  }

  .login-p-error{
    width: 100%;
    text-align: center!important;
  }
}
